const Skills = ['ReactJS', "React Native", "PHP", "ExpressJS", "FeathersJS", "Redux", "SQL", "MongoDB"];

const renderTags = (keywords) => {
    return keywords.map((skill) => {
        return (<React.Fragment><Chip label={skill}/>{" "}</React.Fragment>)
    })
}

const renderProjects = () => {
    return Projects.projects.map((project,index) => {
        return (
            <Grid style={{padding: 25}} key={index}>
                <Grid xs={12} style={{textAlign: 'center',padding:10,fontFamily:'cursive',fontSize:22}}>#{index+1} {project.name}</Grid>
                <Grid xs={12} style={{textAlign: 'center',padding:10}}><i>{project.shortDescription}</i></Grid>
                <Grid xs={12} style={{textAlign: 'center'}}>{project.description}</Grid>
                <Grid xs={12} style={{textAlign: 'center', paddingTop:10}}>{renderTags(project.keywords)}</Grid>
            </Grid>
        )
    })
}

const renderAchievment = (itemArr) => {
    return itemArr.achievement.map((item) => {
        return (
            <ListItem>
                <ListItemIcon>
                    <StarBorder/>
                </ListItemIcon>
                <ListItemText primary={item}/>
            </ListItem>
        )
    })
}

